import React, { useEffect, useState, useRef } from 'react';
import Navbar from './global-components/navbar-v2';
import ErrorPage from './section-components/error';
import Footer from './global-components/footer';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import Pie from './Pie';
import { getElementAtEvent  } from 'react-chartjs-2';
import { TWENTY_FOUR_MOUNTAIN } from '../cm-lib/constants/index'

const color = {
  green: 'rgba(46, 204, 113, 0.3)',
  red: 'rgb( 255, 99, 132, 0.3)',
  gray: 'rgb(192,192,192, 0.3)'
}

const Home_V1 = ({ children, degree, handleClick }) => {

  const labels = TWENTY_FOUR_MOUNTAIN.map(e => e.name)
  

  const data = [...Array(24).keys()].map(() => 100/24)
  const backgroundColor = [
    color.green,
    color.red,
    color.green,
    color.red,
    color.green,
    color.red,
    color.green,
    color.red,

  ]

  return <Pie 
    degree={degree}
    handleClick={(event, dom) => {
        const _selected = getElementAtEvent(dom, event);
        handleClick(event, dom, labels[_selected[0]?.index])
    }}
    backgroundColor={backgroundColor}
    data={data}
    labels={labels}
  >
    { children }
  </Pie>

}

export default Home_V1
